"use client";

import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";
import NotificationDropdown from "@/components/header/NotificationDropdown";
import UserDropdown from "@/components/header/UserDropdown";
import { useSidebar } from "@/context/SidebarContext";
import Image from "next/image";
import Link from "next/link";
import React, { useState, useEffect, useRef } from "react";

const AppHeader: React.FC = () => {
  const [isApplicationMenuOpen, setApplicationMenuOpen] = useState(false);
  const { isMobileOpen, toggleSidebar, toggleMobileSidebar } = useSidebar();
  const inputRef = useRef<HTMLInputElement>(null);

  const handleToggle = () => {
    if (window.innerWidth >= 1024) {
      toggleSidebar();
    } else {
      toggleMobileSidebar();
    }
  };

  const toggleApplicationMenu = () => {
    setApplicationMenuOpen((prev) => !prev);
  };

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if ((event.metaKey || event.ctrlKey) && event.key === "k") {
        event.preventDefault();
        inputRef.current?.focus();
      }
    };

    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  }, []);

  return (
    <header className="sticky top-0 w-full h-14 bg-white border-b border-gray-200 dark:bg-gray-900 dark:border-gray-800">
      <div className="flex items-center justify-between h-full px-3 lg:px-6">
        {/* LEFT SECTION */}
        <div className="flex items-center gap-2">
          {/* SIDEBAR TOGGLE */}
          <button
            onClick={handleToggle}
            className="flex items-center justify-center w-9 h-9 rounded-lg border border-gray-200 text-gray-500 dark:border-gray-800 dark:text-gray-400"
            aria-label="Toggle Sidebar"
          >
            {isMobileOpen ? (
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M18.3 5.71L12 12l6.3 6.29-1.42 1.42L12 13.41l-6.29 6.3-1.42-1.42L10.59 12 4.29 5.71 5.71 4.29 12 10.59l6.29-6.3z" />
              </svg>
            ) : (
              <svg width="18" height="12" viewBox="0 0 18 12" fill="currentColor">
                <path d="M0 1h18v2H0V1zm0 4h12v2H0V5zm0 4h18v2H0V9z" />
              </svg>
            )}
          </button>

          {/* LOGO (MOBILE) */}
          <Link href="/" className="lg:hidden">
            <Image
              src="/images/logo/logo.svg"
              alt="Logo"
              width={130}
              height={28}
              className="dark:hidden"
            />
            <Image
              src="/images/logo/logo-dark.svg"
              alt="Logo"
              width={130}
              height={28}
              className="hidden dark:block"
            />
          </Link>

          {/* SEARCH (DESKTOP) */}
          <div className="hidden lg:block">
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400">
                üîç
              </span>

              <input
                ref={inputRef}
                type="text"
                placeholder="Search or type command..."
                className="h-9 w-[420px] rounded-lg border border-gray-200 bg-transparent pl-10 pr-14 text-sm text-gray-800
                placeholder:text-gray-400 focus:border-brand-300 focus:ring-2 focus:ring-brand-500/10
                dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:placeholder:text-white/30"
              />

              <span className="absolute right-2 top-1/2 -translate-y-1/2 rounded border px-2 py-1 text-xs text-gray-500 dark:border-gray-700 dark:text-gray-400">
                ‚åò K
              </span>
            </div>
          </div>
        </div>

        {/* RIGHT SECTION */}
        <div className="flex items-center gap-2">
          <ThemeToggleButton />
          <NotificationDropdown />
          <UserDropdown />

          {/* MOBILE MENU BUTTON */}
          <button
            onClick={toggleApplicationMenu}
            className="lg:hidden flex items-center justify-center w-9 h-9 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800"
          >
            ‚ãÆ
          </button>
        </div>
      </div>

      {/* MOBILE DROPDOWN */}
      {isApplicationMenuOpen && (
        <div className="flex items-center justify-end gap-3 px-4 py-2 border-t border-gray-200 dark:border-gray-800 lg:hidden">
          <ThemeToggleButton />
          <NotificationDropdown />
          <UserDropdown />
        </div>
      )}
    </header>
  );
};

export default AppHeader;
